import React, { useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faVolumeUp, faVolumeMute, faPlus, faTimes, faMusic, faLock, faLockOpen, faHeadphones, faGripVertical } from '@fortawesome/free-solid-svg-icons';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import './SequencerGrid.css';
import { useAudio } from '../../contexts/AudioContext';

// Import category icons
import kickIcon from '../../assets/icons/kick.webp';
import snareIcon from '../../assets/icons/snare.webp';
import hihatIcon from '../../assets/icons/closed-hat.webp';
import percussionIcon from '../../assets/icons/percussion.webp';
import fxIcon from '../../assets/icons/fx.webp';
import keysIcon from '../../assets/icons/keys.webp';
import vocalIcon from '../../assets/icons/vocal.png';
import icon808 from '../../assets/icons/808.png';
import bassIcon from '../../assets/icons/bass.png';
import clapsIcon from '../../assets/icons/claps.png';

const SequencerGrid = ({ 
  tracks, 
  selectedTrackId, 
  onTrackSelect, 
  onTrackUpdate, 
  onAddTrack, 
  onRemoveTrack,
  isPlaying,
  bpm,
  selectedSample,
  onStepSampleAssign,
  onSoloToggle,
  onLockToggle,
  onTrackReorder
}) => {
  const { 
    currentStep, 
    startSequencer, 
    stopSequencer, 
    updateBpm,
    previewSample
  } = useAudio();

  // Start or stop the sequencer when isPlaying changes
  // Use a ref to track if we've already started the sequencer
  const hasStartedRef = useRef(false);
  
  useEffect(() => {
    if (isPlaying && !hasStartedRef.current) {
      console.log("Starting sequencer with tracks:", tracks);
      startSequencer(tracks);
      hasStartedRef.current = true;
    } else if (!isPlaying && hasStartedRef.current) {
      console.log("Stopping sequencer");
      stopSequencer();
      hasStartedRef.current = false;
    }
    // Only depend on isPlaying, not tracks!
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isPlaying, startSequencer, stopSequencer]);

  // Update BPM in the audio context when it changes
  useEffect(() => {
    updateBpm(bpm);
  }, [bpm, updateBpm]);

  const handleStepToggle = (trackId, stepIndex) => {
    // Find the track
    const track = tracks.find(t => t.id === trackId);
    if (!track) return;
    
    // Don't allow changes if the track is locked
    if (track.locked) return;
    
    // If a sample is selected, assign it to the track and activate the step
    if (selectedSample) {
      // Play the sample when it's assigned
      previewSample(selectedSample.url);
      onStepSampleAssign(trackId, stepIndex);
    } else {
      // Otherwise, just toggle the step
      const updatedSteps = [...track.steps];
      const newState = !updatedSteps[stepIndex];
      updatedSteps[stepIndex] = newState;

      // If the step is being activated and there's a sample, play it
      if (newState && track.sample) {
        previewSample(track.sample.url);
      }

      onTrackUpdate({
        ...track,
        steps: updatedSteps
      });
    }
  };

  const handleVolumeChange = (trackId, volume) => {
    const track = tracks.find(t => t.id === trackId);
    if (!track) return;

    onTrackUpdate({
      ...track,
      volume
    });
  };

  const handleMuteToggle = (trackId) => {
    const track = tracks.find(t => t.id === trackId);
    if (!track) return;

    onTrackUpdate({
      ...track,
      muted: !track.muted
    });
  };

  // Function to get the appropriate icon based on category
  const getCategoryIcon = (category) => {
    switch (category) {
      case 'kick':
        return kickIcon;
      case 'snare':
        return snareIcon;
      case 'claps':
        return clapsIcon;
      case 'highhat':
        return hihatIcon;
      case 'percussion':
        return percussionIcon;
      case 'fx':
        return fxIcon;
      case 'synth':
        return keysIcon;
      case 'vocal':
        return vocalIcon;
      case '808':
        return icon808;
      case 'bass':
        return bassIcon;
      default:
        return null;
    }
  };

  // Function to get the appropriate color based on category
  const getCategoryColor = (category) => {
    switch (category) {
      case 'bass':
        return '#00b894'; // Green
      case '808':
        return '#FFD700'; // Gold
      case 'claps':
        return '#FF6347'; // Tomato
      case 'fx':
        return '#4169E1'; // Royal Blue
      case 'highhat':
        return '#32CD32'; // Lime Green
      case 'kick':
        return '#FF4500'; // Orange Red
      case 'percussion':
        return '#9370DB'; // Medium Purple
      case 'snare':
        return '#FF69B4'; // Hot Pink
      case 'synth':
        return '#00CED1'; // Dark Turquoise
      case 'vocal':
        return '#FF1493'; // Deep Pink
      default:
        return 'var(--color-primary)'; // Default color
    }
  };

  // Handle the end of a drag operation
  const handleDragEnd = (result) => {
    // Dropped outside the list
    if (!result.destination) {
      return;
    }

    // If the position didn't change
    if (result.destination.index === result.source.index) {
      return;
    }

    // Call the parent component's reorder function
    onTrackReorder(result.source.index, result.destination.index);
  };

  return (
    <div className="sequencer-grid">
      <DragDropContext onDragEnd={handleDragEnd}>
        <Droppable droppableId="tracks-list" type="TRACK">
          {(provided) => (
            <div 
              className="tracks-container"
              {...provided.droppableProps}
              ref={provided.innerRef}
            >
              {tracks.map((track, index) => (
                <Draggable 
                  key={track.id.toString()} 
                  draggableId={track.id.toString()} 
                  index={index}
                  isDragDisabled={isPlaying} // Disable dragging during playback
                >
                  {(provided, snapshot) => (
                    <div 
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`track ${selectedTrackId === track.id ? 'selected' : ''} ${snapshot.isDragging ? 'dragging' : ''}`}
                      onClick={() => onTrackSelect(track.id)}
                    >
                      <div className="track-controls">
                        <div 
                          className="drag-handle"
                          {...provided.dragHandleProps}
                          title="Drag to reorder"
                        >
                          <FontAwesomeIcon icon={faGripVertical} />
                        </div>
                        
                        <div className="track-number">
                          {track.sample && track.category ? (
                            <img 
                              src={getCategoryIcon(track.category)} 
                              alt={track.category} 
                              className="track-icon" 
                              width="24"
                              height="24"
                            />
                          ) : (
                            track.id
                          )}
                        </div>
                        
                        <div className="track-controls-right">
                          <div className="track-buttons">
                            <button 
                              className={`track-button mute-button ${track.muted ? 'muted' : ''}`}
                              onClick={(e) => {
                                e.stopPropagation();
                                handleMuteToggle(track.id);
                              }}
                              title={track.muted ? "Unmute" : "Mute"}
                            >
                              <FontAwesomeIcon icon={track.muted ? faVolumeMute : faVolumeUp} />
                            </button>
                            
                            <button 
                              className={`track-button solo-button ${track.solo ? 'active' : ''}`}
                              onClick={(e) => {
                                e.stopPropagation();
                                onSoloToggle(track.id);
                              }}
                              title={track.solo ? "Unsolo" : "Solo"}
                            >
                              <FontAwesomeIcon icon={faHeadphones} />
                            </button>
                            
                            <button 
                              className={`track-button lock-button ${track.locked ? 'active' : ''}`}
                              onClick={(e) => {
                                e.stopPropagation();
                                onLockToggle(track.id);
                              }}
                              title={track.locked ? "Unlock" : "Lock"}
                            >
                              <FontAwesomeIcon icon={track.locked ? faLock : faLockOpen} />
                            </button>
                          </div>
                          
                          <input 
                            type="range" 
                            min="0" 
                            max="100" 
                            value={track.volume} 
                            onChange={(e) => {
                              e.stopPropagation();
                              handleVolumeChange(track.id, parseInt(e.target.value, 10));
                            }}
                            className="volume-slider"
                            disabled={track.locked}
                          />
                        </div>
                      </div>
                      
                      <div className="steps">
                        {track.steps.map((isActive, stepIndex) => (
                          <div 
                            key={stepIndex}
                            className={`step ${isActive ? 'active' : ''}`}
                            style={isActive && track.category ? { backgroundColor: getCategoryColor(track.category) } : {}}
                            onClick={(e) => {
                              e.stopPropagation();
                              handleStepToggle(track.id, stepIndex);
                            }}
                          ></div>
                        ))}
                      </div>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
              
              <div className="step-numbers">
                {Array.from({ length: 32 }, (_, i) => (
                  <div key={i} className="step-number">{i + 1}</div>
                ))}
              </div>
              
              <div className="track-actions-container">
                <div className="track-actions">
                  <button className="add-track-button" onClick={onAddTrack} title="Add Track">
                    <FontAwesomeIcon icon={faPlus} />
                  </button>
                  
                  {selectedTrackId && (
                    <button 
                      className="remove-track-button" 
                      onClick={() => onRemoveTrack(selectedTrackId)}
                      title="Remove Selected Track"
                    >
                      <FontAwesomeIcon icon={faTimes} />
                    </button>
                  )}
                </div>
                <div className="track-instructions">
                  Click a sample in the library to preview it, then click on a grid square to assign it to the track.
                </div>
              </div>
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default SequencerGrid;
