import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faPause, faTrash, faSave, faMusic, faFileExport } from '@fortawesome/free-solid-svg-icons';
import './Header.css';
import logo from '../../assets/beat-maker-logo.png';

const Header = ({ bpm, onBpmChange, isPlaying, onPlayPause, onClearAll, onDemoBeat, onExport, onSave }) => {
  const handleBpmChange = (e) => {
    const value = parseInt(e.target.value, 10);
    if (!isNaN(value) && value >= 60 && value <= 200) {
      onBpmChange(value);
    }
  };

  const handleSliderChange = (e) => {
    const value = parseInt(e.target.value, 10);
    onBpmChange(value);
  };

  return (
    <header className="header">
      <div className="logo-container">
        <img src={logo} alt="Beat Maker Logo" className="logo" />
        <h2>Beat Maker</h2>
      </div>
      
      <div className="controls">
        <button 
          className={`play-button ${isPlaying ? 'playing' : ''}`}
          onClick={onPlayPause}
          aria-label={isPlaying ? 'Pause' : 'Play'}
        >
          <FontAwesomeIcon icon={isPlaying ? faPause : faPlay} />
          {isPlaying ? 'Pause' : 'Play'}
        </button>
        
        <div className="bpm-control">
          <label htmlFor="bpm">BPM:</label>
          <input 
            type="number" 
            id="bpm"
            value={bpm} 
            onChange={handleBpmChange}
            min="60"
            max="200"
          />
          <input 
            type="range" 
            min="60" 
            max="200" 
            value={bpm} 
            onChange={handleSliderChange}
            className="bpm-slider"
          />
        </div>
      </div>
      
      <div className="action-buttons">
        <button 
          className="clear-button"
          onClick={onClearAll}
          aria-label="Clear All"
        >
          <FontAwesomeIcon icon={faTrash} />
          Clear
        </button>
        
        {onDemoBeat && (
          <button 
            className="demo-button"
            onClick={onDemoBeat}
            aria-label="Load Demo Beat"
          >
            <FontAwesomeIcon icon={faMusic} />
            Demo Beat
          </button>
        )}
        
        {onExport && (
          <button 
            className="export-button"
            onClick={onExport}
            aria-label="Export Beat"
          >
            <FontAwesomeIcon icon={faFileExport} />
            Export
          </button>
        )}
        
        {onSave && (
          <button 
            className="save-button"
            onClick={onSave}
            aria-label="Save Beat"
          >
            <FontAwesomeIcon icon={faSave} />
            Save
          </button>
        )}
      </div>
    </header>
  );
};

export default Header;
