import React, { useState, useEffect, useRef } from 'react';
import './App.css';
import Header from './components/Header/Header';
import SequencerGrid from './components/SequencerGrid/SequencerGrid';
import SampleBrowser from './components/SampleBrowser/SampleBrowser';
import Footer from './components/Footer/Footer';
import { AudioProvider, useAudio } from './contexts/AudioContext';
import LoadingOverlay from './components/common/LoadingOverlay';
import ExportDropdown from './components/common/ExportDropdown';
import { Midi } from '@tonejs/midi';
// Import lamejs dynamically since it's not ESM compatible
let lamejs;

const AppContent = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [tracks, setTracks] = useState([]);
  const [selectedTrackId, setSelectedTrackId] = useState(null);
  const [bpm, setBpm] = useState(146);
  const [isPlaying, setIsPlaying] = useState(false);
  const [selectedSample, setSelectedSample] = useState(null);
  const [showExportDropdown, setShowExportDropdown] = useState(false);

  const { 
    updateTracks, 
    isPlaying: contextIsPlaying, 
    loadSample, 
    startSequencer, 
    stopSequencer, 
    updateBpm,
    audioContext 
  } = useAudio();

  // Define callback functions
  const handlePlayPause = React.useCallback(() => {
    // Toggle isPlaying state
    const newIsPlaying = !isPlaying;
    setIsPlaying(newIsPlaying);
    
    // Resume audio context if it's suspended
    if (audioContext && audioContext.state === 'suspended') {
      audioContext.resume();
    }
    
    // Start or stop the sequencer
    if (newIsPlaying) {
      startSequencer(tracks);
    } else {
      stopSequencer();
    }
  }, [isPlaying, audioContext, startSequencer, stopSequencer, tracks]);

  const handleTrackUpdate = (updatedTrack) => {
    setTracks(tracks.map(track => 
      track.id === updatedTrack.id ? updatedTrack : track
    ));
  };

  const handleAddTrack = () => {
    const newTrackId = tracks.length > 0 
      ? Math.max(...tracks.map(track => track.id)) + 1 
      : 1;
    
    const newTrack = {
      id: newTrackId,
      name: `Track ${newTrackId}`,
      sample: null,
      steps: Array(32).fill(false),
      volume: 100,
      muted: false,
      solo: false,
      locked: false
    };
    
    setTracks([...tracks, newTrack]);
    setSelectedTrackId(newTrackId);
  };

  const handleRemoveTrack = (trackId) => {
    // First, filter out the track to be removed
    const filteredTracks = tracks.filter(track => track.id !== trackId);
    
    // Then, renumber the remaining tracks sequentially
    const renumberedTracks = filteredTracks.map((track, index) => ({
      ...track,
      id: index + 1,
      name: `Track ${index + 1}`
    }));
    
    setTracks(renumberedTracks);
    
    // If the removed track was selected, select the last track in the array
    if (selectedTrackId === trackId) {
      setSelectedTrackId(renumberedTracks.length > 0 ? renumberedTracks[renumberedTracks.length - 1].id : null);
    } else if (selectedTrackId > trackId) {
      // If the selected track was after the removed track, adjust its ID
      setSelectedTrackId(selectedTrackId - 1);
    }
  };

  const handleClearAll = () => {
    // Reset all tracks to have no steps activated
    setTracks(tracks.map(track => ({
      ...track,
      steps: Array(32).fill(false)
    })));
    setIsPlaying(false);
  };

  // Initialize with default tracks
  useEffect(() => {
    // Create 8 empty tracks by default
    const initialTracks = Array.from({ length: 8 }, (_, index) => ({
      id: index + 1,
      name: `Track ${index + 1}`,
      sample: null,
      steps: Array(32).fill(false),
      volume: 100,
      muted: false,
      solo: false,
      locked: false
    }));
    
    setTracks(initialTracks);
    setSelectedTrackId(1); // Select the first track by default
    setIsLoading(false);
  }, []);

  // Add spacebar event listener for play/pause
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.code === 'Space' && !event.repeat) {
        // Prevent default space behavior (scrolling)
        event.preventDefault();
        handlePlayPause();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [handlePlayPause]);

  const handleBpmChange = (newBpm) => {
    setBpm(newBpm);
    
    // Update the BPM in the audio context
    updateBpm(newBpm);
  };

  // Store the selected sample without assigning it to a track yet
  const handleSampleSelect = (sample) => {
    setSelectedSample(sample);
  };

  // Assign the selected sample to a specific step in a track
  const handleStepSampleAssign = (trackId, stepIndex) => {
    if (selectedSample && trackId) {
      // Find the track
      const track = tracks.find(t => t.id === trackId);
      if (!track) return;
      
      // Don't allow changes if the track is locked
      if (track.locked) return;

      // Update the track's sample and icon based on the selected sample's category
      const updatedTrack = {
        ...track,
        sample: selectedSample,
        category: selectedSample.category // Store the category to use for the icon
      };

      // Update the track's steps
      const updatedSteps = [...updatedTrack.steps];
      updatedSteps[stepIndex] = true; // Activate the step

      // Update the track with the new sample and activated step
      setTracks(tracks.map(t => 
        t.id === trackId 
          ? { ...updatedTrack, steps: updatedSteps } 
          : t
      ));
    }
  };
  
  // Toggle solo status for a track
  const handleSoloToggle = (trackId) => {
    const track = tracks.find(t => t.id === trackId);
    if (!track) return;
    
    // Toggle solo for the current track
    const isSolo = !track.solo;
    
    // Update all tracks: if any track is soloed, mute all other tracks
    setTracks(tracks.map(t => {
      if (t.id === trackId) {
        // Toggle solo for this track
        return { ...t, solo: isSolo, muted: false };
      } else if (isSolo) {
        // If this track is now soloed, mute all other tracks
        return { ...t, muted: true };
      } else {
        // If no tracks are soloed, unmute all tracks
        const anySoloed = tracks.some(track => track.id !== trackId && track.solo);
        return { ...t, muted: anySoloed };
      }
    }));
  };
  
  // Toggle lock status for a track
  const handleLockToggle = (trackId) => {
    const track = tracks.find(t => t.id === trackId);
    if (!track) return;
    
    // Toggle lock status
    setTracks(tracks.map(t => 
      t.id === trackId ? { ...t, locked: !t.locked } : t
    ));
  };
  
  // Reorder tracks (drag and drop)
  const handleTrackReorder = (sourceIndex, destinationIndex) => {
    // Don't allow reordering during playback
    if (isPlaying) return;
    
    // Create a copy of the tracks array
    const reorderedTracks = [...tracks];
    
    // Remove the track from the source index
    const [movedTrack] = reorderedTracks.splice(sourceIndex, 1);
    
    // Insert the track at the destination index
    reorderedTracks.splice(destinationIndex, 0, movedTrack);
    
    // Renumber the tracks to maintain sequential IDs
    const renumberedTracks = reorderedTracks.map((track, index) => ({
      ...track,
      id: index + 1,
      name: `Track ${index + 1}`
    }));
    
    // Update the tracks state
    setTracks(renumberedTracks);
    
    // If the selected track was moved, update the selection
    if (selectedTrackId === movedTrack.id) {
      setSelectedTrackId(destinationIndex + 1);
    }
  };

  // Load a demo beat with kick, snare, and hi-hat patterns
  const handleDemoBeat = async () => {
    // Stop playback
    setIsPlaying(false);
    
    // Create a demo beat with basic patterns
    const demoTracks = [...tracks];
    
    // Find or create kick, snare, and hi-hat tracks
    let kickTrack = demoTracks.find(t => t.category === 'kick');
    let snareTrack = demoTracks.find(t => t.category === 'snare');
    let hihatTrack = demoTracks.find(t => t.category === 'highhat');
    
    // Create sample objects for the demo
    const kickSample = {
      id: 'demo-kick',
      name: 'Demo Kick',
      url: 'samples/kick/TR 808/ESWTR808 Kick 01.wav',
      category: 'kick',
      subcategory: 'TR 808'
    };
    
    const snareSample = {
      id: 'demo-snare',
      name: 'Demo Snare',
      url: 'samples/snare/TR808/ESWTR808 Snare 01.wav',
      category: 'snare',
      subcategory: 'TR808'
    };
    
    const hihatSample = {
      id: 'demo-hihat',
      name: 'Demo Hi-hat',
      url: 'samples/highhat/TR808/ESWTR808 HH Closed 01.wav',
      category: 'highhat',
      subcategory: 'TR808'
    };
    
    // Preload the samples
    try {
      // Show loading state
      setIsLoading(true);
      
      // Load all samples in parallel
      await Promise.all([
        loadSample(kickSample.url),
        loadSample(snareSample.url),
        loadSample(hihatSample.url)
      ]);
      
      // Hide loading state
      setIsLoading(false);
    } catch (error) {
      console.error("Error preloading demo samples:", error);
      setIsLoading(false);
    }
    
    // Create pattern arrays
    const kickPattern = Array(32).fill(false);
    [0, 8, 16, 24].forEach(i => kickPattern[i] = true); // Kick on beats 1, 3, 5, 7
    
    const snarePattern = Array(32).fill(false);
    [4, 12, 20, 28].forEach(i => snarePattern[i] = true); // Snare on beats 2, 4, 6, 8
    
    const hihatPattern = Array(32).fill(false);
    for (let i = 0; i < 32; i += 2) { // Hi-hat on every 8th note
      hihatPattern[i] = true;
    }
    
    // Update or create the tracks
    if (kickTrack) {
      kickTrack.sample = kickSample;
      kickTrack.steps = kickPattern;
      kickTrack.muted = false;
      kickTrack.solo = false;
    } else {
      // Find the first empty track or create a new one
      const emptyTrackIndex = demoTracks.findIndex(t => !t.sample);
      if (emptyTrackIndex >= 0) {
        demoTracks[emptyTrackIndex] = {
          ...demoTracks[emptyTrackIndex],
          sample: kickSample,
          category: 'kick',
          steps: kickPattern,
          muted: false,
          solo: false
        };
      } else {
        demoTracks.push({
          id: demoTracks.length > 0 ? Math.max(...demoTracks.map(t => t.id)) + 1 : 1,
          name: `Track ${demoTracks.length + 1}`,
          sample: kickSample,
          category: 'kick',
          steps: kickPattern,
          volume: 100,
          muted: false,
          solo: false,
          locked: false
        });
      }
    }
    
    if (snareTrack) {
      snareTrack.sample = snareSample;
      snareTrack.steps = snarePattern;
      snareTrack.muted = false;
      snareTrack.solo = false;
    } else {
      // Find the first empty track or create a new one
      const emptyTrackIndex = demoTracks.findIndex(t => !t.sample && t.id !== (kickTrack?.id || -1));
      if (emptyTrackIndex >= 0) {
        demoTracks[emptyTrackIndex] = {
          ...demoTracks[emptyTrackIndex],
          sample: snareSample,
          category: 'snare',
          steps: snarePattern,
          muted: false,
          solo: false
        };
      } else {
        demoTracks.push({
          id: demoTracks.length > 0 ? Math.max(...demoTracks.map(t => t.id)) + 1 : 1,
          name: `Track ${demoTracks.length + 1}`,
          sample: snareSample,
          category: 'snare',
          steps: snarePattern,
          volume: 100,
          muted: false,
          solo: false,
          locked: false
        });
      }
    }
    
    if (hihatTrack) {
      hihatTrack.sample = hihatSample;
      hihatTrack.steps = hihatPattern;
      hihatTrack.muted = false;
      hihatTrack.solo = false;
    } else {
      // Find the first empty track or create a new one
      const emptyTrackIndex = demoTracks.findIndex(t => 
        !t.sample && 
        t.id !== (kickTrack?.id || -1) && 
        t.id !== (snareTrack?.id || -1)
      );
      if (emptyTrackIndex >= 0) {
        demoTracks[emptyTrackIndex] = {
          ...demoTracks[emptyTrackIndex],
          sample: hihatSample,
          category: 'highhat',
          steps: hihatPattern,
          muted: false,
          solo: false
        };
      } else {
        demoTracks.push({
          id: demoTracks.length > 0 ? Math.max(...demoTracks.map(t => t.id)) + 1 : 1,
          name: `Track ${demoTracks.length + 1}`,
          sample: hihatSample,
          category: 'highhat',
          steps: hihatPattern,
          volume: 100,
          muted: false,
          solo: false,
          locked: false
        });
      }
    }
    
    // Update the tracks state
    setTracks(demoTracks);
    
    // Set BPM to a good demo value
    setBpm(120);
  };

  // Handle exporting the beat
  const handleExport = () => {
    // Show the export dropdown
    setShowExportDropdown(true);
  };
  
  // Handle export format selection
  const handleExportFormatSelect = (format, repetitions) => {
    if (format === 'mp3') {
      exportMP3(repetitions);
    } else {
      exportMIDI(repetitions);
    }
  };
  
  // Close the export dropdown
  const closeExportDropdown = () => {
    setShowExportDropdown(false);
  };
  
  // Export the beat as an MP3 file
  const exportMP3 = async (repetitions = 1) => {
    try {
      setIsLoading(true);
      
      // Dynamically import lamejs
      if (!lamejs) {
        try {
          // Use a CDN version since the npm version is not ESM compatible
          const script = document.createElement('script');
          script.src = 'https://cdn.jsdelivr.net/npm/lamejs@1.2.1/lame.all.min.js';
          script.async = true;
          
          await new Promise((resolve, reject) => {
            script.onload = resolve;
            script.onerror = reject;
            document.head.appendChild(script);
          });
          
          // Wait for the global Lame object to be available
          // Give it a moment to initialize
          await new Promise(resolve => setTimeout(resolve, 500));
          
          if (typeof window.Lame !== 'undefined') {
            lamejs = window.Lame;
          } else if (typeof lamejs !== 'undefined') {
            // Already defined
          } else if (typeof window.lamejs !== 'undefined') {
            lamejs = window.lamejs;
          } else {
            console.error('Lame library loaded but object not found');
            // Try to continue anyway
            lamejs = { Mp3Encoder: function() { 
              alert('MP3 encoding not available. Please try MIDI export instead.');
              throw new Error('MP3 encoding not available');
            }};
          }
        } catch (error) {
          console.error('Error loading lamejs:', error);
          alert('Error loading MP3 encoder. Please try MIDI export instead.');
          setIsLoading(false);
          return;
        }
      }
      
      // Create an offline audio context for rendering
      const offlineCtx = new (window.OfflineAudioContext || window.webkitOfflineAudioContext)(
        2, // stereo
        44100 * 60, // 60 seconds at 44.1kHz
        44100 // sample rate
      );
      
      // Create a gain node for the master output
      const masterGain = offlineCtx.createGain();
      masterGain.gain.value = 1.0;
      masterGain.connect(offlineCtx.destination);
      
      // Calculate the duration of one pattern (in seconds)
      const patternDuration = (60 / bpm) * 8; // 8 beats in a pattern
      const totalDuration = patternDuration * repetitions;
      
      // Load all samples used in the tracks
      const loadedBuffers = {};
      const loadPromises = [];
      
      // Find all unique samples used in tracks
      const uniqueSamples = new Set();
      tracks.forEach(track => {
        if (track.sample && track.sample.url) {
          uniqueSamples.add(track.sample.url);
        }
      });
      
      // Load each unique sample
      uniqueSamples.forEach(url => {
        // Use process.env.PUBLIC_URL to get the correct path for GitHub Pages
        const fullUrl = process.env.PUBLIC_URL + `/${url}`;
        
        const loadPromise = fetch(fullUrl)
          .then(response => {
            if (!response.ok) {
              throw new Error(`Failed to fetch audio file: ${response.status} ${response.statusText}`);
            }
            return response.arrayBuffer();
          })
          .then(arrayBuffer => offlineCtx.decodeAudioData(arrayBuffer))
          .then(audioBuffer => {
            loadedBuffers[url] = audioBuffer;
          })
          .catch(error => {
            console.error(`Error loading sample ${url}:`, error);
            console.log(`URL that failed: ${fullUrl}`);
          });
        
        loadPromises.push(loadPromise);
      });
      
      // Wait for all samples to load
      await Promise.all(loadPromises);
      
      // Schedule all notes in the pattern, repeated as requested
      for (let rep = 0; rep < repetitions; rep++) {
        const startTime = rep * patternDuration;
        
        tracks.forEach(track => {
          if (!track.sample || !track.sample.url || track.muted) return;
          
          const buffer = loadedBuffers[track.sample.url];
          if (!buffer) return;
          
          // Get the volume for this track
          const volume = track.volume / 100;
          
          // Schedule each active step
          track.steps.forEach((isActive, stepIndex) => {
            if (!isActive) return;
            
            // Calculate when this step should play
            const time = startTime + (stepIndex / 32) * patternDuration;
            
            // Create a source node
            const source = offlineCtx.createBufferSource();
            source.buffer = buffer;
            
            // Create a gain node for volume
            const gainNode = offlineCtx.createGain();
            gainNode.gain.value = volume;
            
            // Connect the nodes
            source.connect(gainNode);
            gainNode.connect(masterGain);
            
            // Schedule the playback
            source.start(time);
          });
        });
      }
      
      // Render the audio
      const renderedBuffer = await offlineCtx.startRendering();
      
      // Convert the rendered buffer to an MP3 file
      // Check if Mp3Encoder is available
      if (!lamejs.Mp3Encoder) {
        console.error('Mp3Encoder not found in lamejs library');
        alert('MP3 encoding not available. Please try MIDI export instead.');
        setIsLoading(false);
        return;
      }
      
      const mp3Encoder = new lamejs.Mp3Encoder(2, 44100, 128);
      const mp3Data = [];
      
      // Process the audio data
      const left = renderedBuffer.getChannelData(0);
      const right = renderedBuffer.getChannelData(1);
      const sampleBlockSize = 1152;
      
      for (let i = 0; i < left.length; i += sampleBlockSize) {
        const leftChunk = left.subarray(i, i + sampleBlockSize);
        const rightChunk = right.subarray(i, i + sampleBlockSize);
        
        // Convert float32 to int16
        const leftInt16 = new Int16Array(leftChunk.length);
        const rightInt16 = new Int16Array(rightChunk.length);
        
        for (let j = 0; j < leftChunk.length; j++) {
          // Scale to int16 range and clip
          leftInt16[j] = Math.max(-32768, Math.min(32767, leftChunk[j] * 32768));
          rightInt16[j] = Math.max(-32768, Math.min(32767, rightChunk[j] * 32768));
        }
        
        // Encode the chunks
        const mp3buf = mp3Encoder.encodeBuffer(leftInt16, rightInt16);
        if (mp3buf.length > 0) {
          mp3Data.push(mp3buf);
        }
      }
      
      // Finalize the encoding
      const mp3buf = mp3Encoder.flush();
      if (mp3buf.length > 0) {
        mp3Data.push(mp3buf);
      }
      
      // Create a Blob from the MP3 data
      const blob = new Blob(mp3Data, { type: 'audio/mp3' });
      
      // Create a download link
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'beat-maker-export.mp3';
      a.click();
      
      // Clean up
      URL.revokeObjectURL(url);
      setIsLoading(false);
      
      alert('MP3 export complete!');
    } catch (error) {
      console.error('Error exporting MP3:', error);
      setIsLoading(false);
      alert('Error exporting MP3. Please try again.');
    }
  };
  
  // Export the beat as a MIDI file
  const exportMIDI = (repetitions = 1) => {
    try {
      setIsLoading(true);
      
      // Create a new MIDI file
      const midi = new Midi();
      
      // Add a track for each instrument
      tracks.forEach((track, trackIndex) => {
        if (!track.sample || track.muted) return;
        
        // Create a new track
        const midiTrack = midi.addTrack();
        
        // Set the track name based on the sample
        midiTrack.name = track.sample.name || `Track ${trackIndex + 1}`;
        
        // Map different sample categories to different MIDI notes
        let midiNote = 60; // Default to middle C
        
        switch (track.category) {
          case 'kick':
            midiNote = 36; // Bass Drum 1
            break;
          case 'snare':
            midiNote = 38; // Snare Drum 1
            break;
          case 'highhat':
            midiNote = 42; // Closed Hi-hat
            break;
          case 'claps':
            midiNote = 39; // Hand Clap
            break;
          case 'percussion':
            midiNote = 43; // High Floor Tom
            break;
          case 'fx':
            midiNote = 55; // Splash Cymbal
            break;
          default:
            midiNote = 60; // Middle C
        }
        
        // Add notes for each active step, repeated as requested
        for (let rep = 0; rep < repetitions; rep++) {
          const patternOffset = rep * 8; // 8 beats per pattern (2 bars)
          
          track.steps.forEach((isActive, stepIndex) => {
            if (!isActive) return;
            
            // Calculate the time for this step (in quarter notes)
            const time = patternOffset + stepIndex * 0.25; // 16th notes
            
            // Add a note
            midiTrack.addNote({
              midi: midiNote,
              time: time,
              duration: 0.25, // 16th note duration
              velocity: track.volume / 100 // Use track volume for velocity
            });
          });
        }
      });
      
      // Convert to a MIDI file
      const midiData = midi.toArray();
      
      // Create a Blob from the MIDI data
      const blob = new Blob([midiData], { type: 'audio/midi' });
      
      // Create a download link
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'beat-maker-export.mid';
      a.click();
      
      // Clean up
      URL.revokeObjectURL(url);
      setIsLoading(false);
      
      alert('MIDI export complete!');
    } catch (error) {
      console.error('Error exporting MIDI:', error);
      setIsLoading(false);
      alert('Error exporting MIDI. Please try again.');
    }
  };
  
  // Update tracks in the audio context when they change during playback
  useEffect(() => {
    if (isPlaying) {
      updateTracks(tracks);
    }
  }, [tracks, isPlaying, updateTracks]);

  return (
    <div className="app">
      {isLoading && <LoadingOverlay />}
      {showExportDropdown && (
        <ExportDropdown 
          onSelect={handleExportFormatSelect} 
          onClose={closeExportDropdown} 
        />
      )}
      
      <Header 
        bpm={bpm}
        onBpmChange={handleBpmChange}
        isPlaying={isPlaying}
        onPlayPause={handlePlayPause}
        onClearAll={handleClearAll}
        onDemoBeat={handleDemoBeat}
        onExport={handleExport}
      />
      
      <div className="main-content">
        <div className="sequencer-section">
          <SequencerGrid 
            tracks={tracks}
            selectedTrackId={selectedTrackId}
            onTrackSelect={setSelectedTrackId}
            onTrackUpdate={handleTrackUpdate}
            onAddTrack={handleAddTrack}
            onRemoveTrack={handleRemoveTrack}
            isPlaying={isPlaying}
            bpm={bpm}
            selectedSample={selectedSample}
            onStepSampleAssign={handleStepSampleAssign}
            onSoloToggle={handleSoloToggle}
            onLockToggle={handleLockToggle}
            onTrackReorder={handleTrackReorder}
          />
        </div>
        
        <div className="browser-section">
          <SampleBrowser 
            selectedTrackId={selectedTrackId}
            onSampleSelect={handleSampleSelect}
          />
        </div>
      </div>
      <Footer />
    </div>
  );
};

function App() {
  return (
    <AudioProvider>
      <AppContent />
    </AudioProvider>
  );
}

export default App;
