import React, { createContext, useContext, useState, useEffect, useRef } from 'react';

const AudioContext = createContext();

export const useAudio = () => useContext(AudioContext);

export const AudioProvider = ({ children }) => {
  const [audioContext, setAudioContext] = useState(null);
  const [masterGain, setMasterGain] = useState(null);
  const [isInitialized, setIsInitialized] = useState(false);
  const [currentStep, setCurrentStep] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [bpm, setBpm] = useState(146);
  const [loadedSamples, setLoadedSamples] = useState({});
  const [currentlyPlayingSample, setCurrentlyPlayingSample] = useState(null);
  
  // Refs for sequencer
  const intervalRef = useRef(null);
  const tracksRef = useRef([]);
  const stepRef = useRef(0);

  // Initialize the audio context
  useEffect(() => {
    try {
      const AudioContextClass = window.AudioContext || window.webkitAudioContext;
      const newAudioContext = new AudioContextClass();
      const newMasterGain = newAudioContext.createGain();
      newMasterGain.gain.value = 1.0;
      newMasterGain.connect(newAudioContext.destination);
      
      setAudioContext(newAudioContext);
      setMasterGain(newMasterGain);
      setIsInitialized(true);
      console.log("Audio context initialized");
    } catch (error) {
      console.error("Failed to initialize audio context:", error);
    }
  }, []);

  // Clean up on unmount
  useEffect(() => {
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, []);

  // Load a sample from a URL
  const loadSample = async (url) => {
    if (!audioContext) {
      console.error("Audio context not initialized");
      return null;
    }
    
    // Check if sample is already loaded
    if (loadedSamples[url]) {
      return loadedSamples[url];
    }
    
    try {
      // In a React app, we need to make sure we're accessing the files correctly
      // For files in the public folder, we need to use the root path
      // For GitHub Pages, we need to use the repository name as a prefix
      const fullUrl = process.env.PUBLIC_URL + `/${url}`;
      
      // Fetch the audio file
      const response = await fetch(fullUrl);
      if (!response.ok) {
        throw new Error(`Failed to fetch audio file: ${response.status} ${response.statusText}`);
      }
      
      // Get the audio data as an ArrayBuffer
      const arrayBuffer = await response.arrayBuffer();
      
      // Decode the audio data
      const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
      
      // Store the loaded sample
      setLoadedSamples(prev => ({
        ...prev,
        [url]: audioBuffer
      }));
      
      return audioBuffer;
    } catch (error) {
      console.error('Error loading sample:', error);
      console.log(`URL that failed: ${url}`);
      return null;
    }
  };

  // Play a sample
  const playSample = (buffer, time = 0, volume = 1.0) => {
    if (!audioContext || !buffer) {
      console.error("Cannot play sample: audioContext or buffer is null");
      return;
    }
    
    const source = audioContext.createBufferSource();
    source.buffer = buffer;
    
    const gainNode = audioContext.createGain();
    gainNode.gain.value = volume;
    
    source.connect(gainNode);
    gainNode.connect(masterGain);
    
    source.start(time);
    return source;
  };

  // Preview a sample (for sample browser)
  const previewSample = async (url) => {
    if (!audioContext) {
      console.log("Audio context not initialized");
      return;
    }
    
    try {
      // Resume audio context if it's suspended
      if (audioContext.state === 'suspended') {
        await audioContext.resume();
      }
      
      // Stop any currently playing preview
      if (currentlyPlayingSample) {
        try {
          currentlyPlayingSample.stop();
        } catch (e) {
          console.log("Error stopping current sample:", e);
        }
        setCurrentlyPlayingSample(null);
      }
      
      // Add visual feedback for the currently playing sample
      const sampleElements = document.querySelectorAll('.sample-item');
      sampleElements.forEach(el => {
        el.classList.remove('playing');
      });
      
      // Find the element that corresponds to this sample and add a playing class
      const sampleElement = Array.from(sampleElements).find(el => 
        el.getAttribute('data-sample-url') === url
      );
      
      if (sampleElement) {
        sampleElement.classList.add('playing');
      }
      
      const buffer = await loadSample(url);
      if (buffer) {
        const source = playSample(buffer);
        setCurrentlyPlayingSample(source);
        
        // Remove the playing class when the sample finishes playing
        if (source) {
          source.onended = () => {
            if (sampleElement) {
              sampleElement.classList.remove('playing');
            }
            setCurrentlyPlayingSample(null);
          };
        }
      }
    } catch (error) {
      console.error("Error previewing sample:", error);
    }
  };

  // Simple sequencer function - plays the current step and advances to the next
  const playStep = () => {
    if (!audioContext) {
      console.error("[DEBUG] playStep: audioContext is null");
      return;
    }

    console.log(`[DEBUG] playStep: stepRef.current=${stepRef.current}, currentStep=${currentStep}`);

    // Check if any track is soloed
    const anySoloed = tracksRef.current.some(track => track && track.solo);

    // Play all active samples for the current step
    tracksRef.current.forEach((track, tIdx) => {
      if (!track) {
        console.warn(`[DEBUG] playStep: track ${tIdx} is undefined`);
        return;
      }
      if (!Array.isArray(track.steps)) {
        console.warn(`[DEBUG] playStep: track ${tIdx} steps is not an array`, track.steps);
        return;
      }
      
      // Only play if:
      // 1. Track is not muted AND
      // 2. Either no tracks are soloed OR this track is soloed
      if (track.sample && 
          track.steps[stepRef.current] && 
          !track.muted && 
          (!anySoloed || track.solo)) {
        const volume = track.volume / 100;
        if (loadedSamples[track.sample.url]) {
          console.log(`[DEBUG] playStep: Playing sample for track ${tIdx} at step ${stepRef.current}: ${track.sample.url}`);
          playSample(loadedSamples[track.sample.url], 0, volume);
        } else {
          console.warn(`[DEBUG] playStep: Sample not loaded for track ${tIdx}: ${track.sample.url}`);
        }
      }
    });

    // Advance to the next step
    stepRef.current = (stepRef.current + 1) % 32;
    
    // Update the UI directly instead of using React state
    // This prevents unnecessary re-renders
    document.querySelectorAll('.step').forEach((el, i) => {
      const stepIndex = i % 32;
      if (stepIndex === stepRef.current) {
        el.classList.add('current');
      } else {
        el.classList.remove('current');
      }
    });
    
    // Don't update React state at all during playback
    // setCurrentStep(stepRef.current);

    // Debug log
    console.log(`[DEBUG] Sequencer tick, stepRef.current=${stepRef.current}, currentStep=${currentStep}`);
  };

  // Start the sequencer
  const startSequencer = (tracks) => {
    // Make sure we have an audio context
    if (!audioContext) {
      console.error("[DEBUG] Cannot start sequencer: audioContext is null");
      return;
    }

    // Resume audio context if it's suspended
    if (audioContext.state === 'suspended') {
      audioContext.resume();
    }

    // Stop any existing interval
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }

    // Reset step ref and state
    stepRef.current = 0;
    setCurrentStep(0);

    // Store tracks in ref for access in interval
    tracksRef.current = [...tracks];

    // Calculate interval time based on BPM (60000 ms / BPM / 4 for 16th notes)
    const intervalTime = Math.floor(60000 / bpm / 4);

    console.log("[DEBUG] Starting sequencer with interval:", intervalTime, "ms");
    console.log("[DEBUG] Tracks:", tracksRef.current.map((t, i) => ({
      id: t.id,
      steps: t.steps,
      sample: t.sample ? t.sample.url : null
    })));

    // Start a new interval
    intervalRef.current = setInterval(() => {
      console.log(`[DEBUG] Interval tick, intervalRef.current=${intervalRef.current}`);
      playStep();
    }, intervalTime);

    setIsPlaying(true);
    console.log("[DEBUG] Sequencer started, intervalRef.current=", intervalRef.current);
  };

  // Stop the sequencer
  const stopSequencer = () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      console.log("[DEBUG] Cleared intervalRef.current:", intervalRef.current);
      intervalRef.current = null;
    }

    setIsPlaying(false);
    stepRef.current = 0;
    
    // Only update React state when stopping
    setCurrentStep(0);
    
    // Clear all current step indicators
    document.querySelectorAll('.step').forEach(el => {
      el.classList.remove('current');
    });

    console.log("[DEBUG] Sequencer stopped");
  };

  // Update BPM
  const updateBpm = (newBpm) => {
    setBpm(newBpm);
    
    // If playing, restart with new BPM
    if (isPlaying && intervalRef.current) {
      stopSequencer();
      startSequencer(tracksRef.current);
    }
  };

  // Update tracks while sequencer is playing
  const updateTracks = (newTracks) => {
    // Update the tracks ref with the new tracks
    tracksRef.current = [...newTracks];
    console.log("[DEBUG] Updated tracks during playback:", tracksRef.current);
  };

  const value = {
    audioContext,
    masterGain,
    isInitialized,
    currentStep,
    isPlaying,
    bpm,
    loadSample,
    playSample,
    previewSample,
    startSequencer,
    stopSequencer,
    updateBpm,
    updateTracks
  };

  return (
    <AudioContext.Provider value={value}>
      {children}
    </AudioContext.Provider>
  );
};
