import React, { useState } from 'react';
import './ExportDropdown.css';

const ExportDropdown = ({ onSelect, onClose }) => {
  const [repetitions, setRepetitions] = useState(1);

  const handleSelect = (format) => {
    onSelect(format, repetitions);
    onClose();
  };

  const handleRepetitionChange = (e) => {
    const value = parseInt(e.target.value, 10);
    if (!isNaN(value) && value >= 1 && value <= 8) {
      setRepetitions(value);
    }
  };

  return (
    <div className="export-dropdown-container">
      <div className="export-dropdown">
        <div className="export-dropdown-header">
          <h3>Export Options</h3>
        </div>
        
        <div className="export-dropdown-content">
          <div className="export-repetitions">
            <label htmlFor="repetitions">Repeat Beat:</label>
            <div className="repetition-control">
              <button 
                className="repetition-button"
                onClick={() => setRepetitions(Math.max(1, repetitions - 1))}
                disabled={repetitions <= 1}
              >
                -
              </button>
              <input 
                type="number" 
                id="repetitions"
                value={repetitions} 
                onChange={handleRepetitionChange}
                min="1"
                max="8"
              />
              <button 
                className="repetition-button"
                onClick={() => setRepetitions(Math.min(8, repetitions + 1))}
                disabled={repetitions >= 8}
              >
                +
              </button>
              <span className="repetition-label">times</span>
            </div>
          </div>
          
          <div className="export-format-label">Export Format:</div>
          <div className="export-dropdown-options">
            <button 
              className="export-option mp3-option"
              onClick={() => handleSelect('mp3')}
            >
              MP3
            </button>
            <button 
              className="export-option midi-option"
              onClick={() => handleSelect('midi')}
            >
              MIDI
            </button>
          </div>
        </div>
      </div>
      <div className="export-dropdown-backdrop" onClick={onClose}></div>
    </div>
  );
};

export default ExportDropdown;
